<template>
<div class="intellBox">
    <h4 class="lz-title-h4" v-if="pageLevel==0">请选择被保人告知异常所属模块，<br>如不在列表中则不能投保。</h4>
    <h4 class="lz-title-h4" v-else-if="pageLevel>0">选择具体疾病名称</h4>
    <ill-list :illList="illList" :pageLevel="pageLevel" @result-index="onillList"></ill-list>
    <div class="lz-isIndex" v-if="desLevel>0" @click="resultFun(0)">上一步</div>
</div>
</template>
<script>
  import Vue from "vue";
  import motheds from "@/api/motheds";
  import illListPon from "@/components/illList"; //疾病列表组件
  import { mapMutations } from "vuex";
  import { mapGetters } from "vuex";
  import _ from "lodash";
  export default {
    name: "Index",
    data() {
      return {
        illList: [], //疾病子组件列表
        pageLevel: 0, // 页面等级 
        desLevel:0,//疾病等级
        fromvalue: "", // 测算页fromvalue
        diseseAll:null
      };
    },
    created() { 
      // 获取地址栏key 与code
      if (this.desLevel == 0 && this.$route.query.key && this.$route.query.proCode) {
        var data = {
          key: this.$route.query.key,
          code: this.$route.query.proCode
        };
        this.intell.paperId = "";
        this.intell.indexList = []; //初始化列表
        this.intell.int_quey = data; //初始化参数
        this.set_msg({ key: "intelligent", data: this.intell }); 
      }
    },
    mounted() {
      this.getParams();
    },
    methods: {
      getParams(){
        this.pageLevel = parseInt(this.$route.params.pageLevel); // 页面等级（当前所选疾病的页面等级）
        this.desLevel =  parseInt(this.$route.params.desLevel); // 疾病等级（流程大疾病等级）
        console.log(this.$route.params)
        if(this.desLevel >0 && this.pageLevel == 0){
          this.diseseAll = this.intell.indexList.length >0 ?this.intell.indexList:null;
        }
        if(!this.diseseAll){
          this.getInform()
        }else{
          this.illListFn()
        };
      },
      //获取测算页带过来的fromvalue值
      getInform() {
        this.$http.getHttp({
            name: "getInfo",
            params: [{ name: "key", val: this.intell.int_quey.key }],
            header: 2
          },res => {
            var datas = JSON.parse(res.data);
            console.log(datas);
            sessionStorage.setItem("productProcess",JSON.stringify(datas.info.productProcess))
            this.fromvalue = datas.info.writeInform.fromvalue;
            this.getIllList(this.fromvalue); //疾病Ajax请求
          });
      },
      //请求疾病列表
      getIllList(fromvalue) {
          var load = this.$popup.loading("正在请求智能核保数据...");
          this.$http.getHttp({
            name: "qusetionList",
            params: {
              fromvalue: fromvalue
            },
            code: this.intell.int_quey.code
          },
          res => {
            console.log(res)
            this.$popup.closeLay(load);
            this.diseseAll = res.data.riskData //疾病列表全部数据
            this.intell.paperId = res.data.paperId; //paperId 缓存
            this.intell.indexList = this.diseseAll; //首页数据缓存
            this.set_msg({key: "intelligent",data: this.intell}); //将当前选择项缓存
            this.illListFn()
          }
        );
      },
      //疾病列表大于2级页子组件赋值
      illListFn() {
         var self = this;
        var index = this.pageLevel;
        if(this.pageLevel>0){
          this.intell.issList.splice(this.desLevel)//删掉当前等级之后的问题列表
          var arrList = this.diseseAll;
          _.forEach(this.intell.diseseList[this.desLevel], (v, i) => {
            arrList = arrList[v];
            if (i == index) return;
          });
           this.illList = arrList.subDiseaseList;
        }else{
          this.intell.diseseList[this.desLevel] = [];
          this.illList = this.diseseAll
        }
      },
      //点击切换列表 跳转  赋值列表数据
      onillList(list, i) {
        if (list.subDiseaseList) {
          this.intell.diseseList[this.desLevel][this.pageLevel] = i;
          this.set_msg({key: "intelligent",lev:'diseseList',data: this.intell.diseseList}); //将当前选择项缓存
          this.$router.push({ path:"/index4/"+(this.pageLevel+1)+"/"+this.desLevel });
        } else if (list.result) {
          if(_.findIndex(this.intell.issList,v=>v.diseaseCode == list.diseaseCode)>-1)return this.$popup.msg("已选择当前疾病")
          //  //结果存在跳结果页
          this.intell.issList[this.desLevel] = list;
          this.set_msg({ key: "intelligent",lev: "issList", data: this.intell.issList}); //存储问题列表
          this.$router.push({ name:'IssueList4',query:{desLevel:this.desLevel}});
        } else if (list.diseaseQuestion) {
          if(_.findIndex(this.intell.issList,v=>v.diseaseCode ==list.diseaseCode)>-1)return this.$popup.msg("已选择当前疾病")
          this.intell.issList[this.desLevel] = list
          //问题存在跳问题页
          this.set_msg({ key: "intelligent",lev: "issList", data: this.intell.issList}); //存储问题列表
          this.$router.push({ path: "/issue4/0/"+this.desLevel});
        }
      },
      //上一部
      resultFun(type) {
        switch (type) {
          case 0:
            this.$router.go(-1);
            break;
        }
      },
      ...mapMutations({
        set_msg: "SET_MESSAGE"
      })
    },
    computed: {
      // 使用对象展开运算符将 getter 混入 computed 对象中
      ...mapGetters([
        "intell"
        // ...
      ])
    },
    components: {
      "ill-list": illListPon
    },
    watch:{
         '$route':'getParams'
    },
  };
</script>

<style lang="scss">
.intellBox {
  min-height: 100%;
  background-color: #fff;
  overflow: hidden;
  .lz-isIndex{
    margin: 0 auto 20px;
  }
}
</style>


